import React, { useState } from "react";
import "./styles/App.css";

import Data from "./data";
import Map from "./components/Map";
import Table from "./components/Table";
import VolumeSlider from "./components/Slider";

interface store {
	name: string;
	address: string;
	phone: string;
	coordinates: {
		lat: number;
		lng: number;
	};
	sales: {
		retail: number;
		service: number;
	};
}

interface coordinates {
	lat: number;
	lng: number;
}

function App() {
	const [zoom, setZoom] = useState<number>(9);
	const [stores, setStores] = useState<store[]>(Data.stores as store[]);
	const [volumeRange, setVolumeRange] = useState<readonly number[]>([8, 14]);
	const [center, setCenter] = useState<coordinates>({ lat: 43.65107, lng: -79.347015 });

	const getVolume = ({ retail, service }): number => {
		return retail + service;
	};

	const inRange = (volume: number, range): boolean => {
		return volume > range[0] * 1000000 && volume < range[1] * 1000000;
	};

	const getStoreCoordinatesByName = (name: string): coordinates => {
		return Data.stores.filter((store) => store.name === name)[0].coordinates;
	};

	const focusStore = (event, rowData) => {
		setZoom(13);
		setCenter(getStoreCoordinatesByName(rowData.name));
	};

	const updateStores = () => {
		setStores(Data.stores.filter((store) => inRange(getVolume(store.sales), volumeRange)));
		console.log("updateStores");
	};

	const onSliderUpdate = (values: readonly number[]) => {
		// if (volumeRange !== values) {
		// 	setVolumeRange(values);
		// 	updateStores();
		// 	console.log("onSliderUpdate");
		// }
	};

	const onSliderChange = (values: readonly number[]) => {
		if (volumeRange !== values) {
			setVolumeRange(values);
			updateStores();
			console.log("onSliderChange");
		}
	};

	// const getStores = () => {
	// 	return Data.stores.filter((store) => inRange(getVolume(store.sales), volumeRange));
	// };

	console.log("app");

	return (
		<div className="App">
			<Map apiKey={{ key: "AIzaSyCg5g5-VTNN02rjrHVb7lzWE4kYoQb7f_8" }} center={center} zoom={zoom} stores={stores} />
			<VolumeSlider volumeRange={volumeRange} onChange={onSliderChange} onUpdate={onSliderUpdate} />
			{/* <VolumeSlider volumeRange={volumeRange} setVolumeRange={setVolumeRange} /> */}
			<Table stores={stores} onRowClick={focusStore} />
		</div>
	);
}

export default App;
